.image {
    display: block;
    width: 100%;
    height: auto;
  }
  
  
  .capsule:hover .overlay {
    opacity: 1;
  }
  .capsule a {
      color: white;
  }
  .capsule a{
      padding: 60px;
  }
  .capsule a:hover {
    color:#EE82EE;
  }
  .overlay {
    position: absolute; 
    bottom: 0; 
    background: rgb(0, 0, 0);
    background: rgba(0, 0, 0, 0.5); /* Black see-through */
    color: #f1f1f1; 
    width: 100%;
    transition: .5s ease;
    opacity:0;
    color: white;
    font-size: 20px;
    padding: 40px;
    text-align: center;
  }
